import React from 'react'
import { OptionsStyled } from './Options.styled'
import { FontAwesomeIcon as FA } from '@fortawesome/react-fontawesome'
import { faAngleDown, faAngleUp } from '@fortawesome/pro-regular-svg-icons'
import { LinkToToggleOptionsStyled } from './Options.styled'
import { Radio } from 'antd'

const Options = function(props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that
  console.log('that.props.search_options', that.props.search_options)
  /*
   * View
   */
  return (
    <OptionsStyled className={(that.state.showOptions ? ' show' : ' hide')}>
      <div className="content">
        {/*
         * Show WIP
         */}
        <LinkToToggleOptionsStyled
          className="LinkToToggleOptionsStyled"
          onClick={that.toggleOptions}
        >
          <span className="hide-small">{(that.state.showOptions ? 'hide' : 'more')}&nbsp;</span>
          <span>API options&nbsp;</span>
          {that.state.showOptions
            ? <FA icon={faAngleUp} className="faAngleUp" />
            : <FA icon={faAngleDown} className="faAngleDown" />
          }
        </LinkToToggleOptionsStyled>

        {/*
         * MORE OPTIONS
         */}
        <span className="show-large">
          sort by:&nbsp;&nbsp;
        </span>
        <Radio.Group
          defaultValue={that.props.search_options.length_vs_relevance}
          size="small"
          onChange={(e) => {
            console.log('changed', e.target.value)
            that.props.word_actions.RX_search_options({ length_vs_relevance: e.target.value })
          }}
        >
            <Radio.Button value={0.5}>&nbsp;shortest <span className="hide-small">name</span></Radio.Button>
            <Radio.Button value={2.5}>both</Radio.Button>
            <Radio.Button value={50}>most relevant&nbsp;</Radio.Button>
            </Radio.Group>

            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <p>...</p>
            <p>...</p>
            <p>...</p>
            <p>...</p>
            <p>...</p>
            <p>...</p>
            </div>
            </OptionsStyled>
  )
}

export default Options
