import React from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import * as ui_actions from 'src/redux/actions/ui'
import * as word_actions from 'src/redux/actions/word'
import * as domain_actions from 'src/redux/actions/domain'
import { StyledPage } from '../../components/Layout.styled'
import DomainsResults from './DomainsResults'
import DomainsHome from './DomainsHome'
import Search from '../../components/Search'
import { faArrowRight } from '@fortawesome/pro-regular-svg-icons'
import { FontAwesomeIcon as FA } from '@fortawesome/react-fontawesome'
// import { Tooltip } from 'antd'
// import Tip from '../../components/Tip'

class Domains extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      tldAdd: '',
      showWip: false,
      showOptions: false,
      gotAvailability: true, // must start out as true!
    }
  }

  async componentDidUpdate(prevProps) {
    /*
     * Get availability (if new suggestions)
     */
    if (
      this.state.gotAvailability &&
      this.props.suggestions_domains.length &&
      this.props.suggestions_domains !== prevProps.suggestions_domains
    ) {
      this.setState({
        gotAvailability: false,
      })
      // fetch availability
      await this.props.domain_actions.RX__domain_suggestions_availability()
      this.setState({
        gotAvailability: true,
      })
    }
  }

  toggleResults = listType => {
    this.props.domain_actions.RX__results_toggle_list(listType)
  }
  toggleWip = () => {
    this.setState({
      showWip: !this.state.showWip,
    })
  }
  toggleOptions = () => {
    this.setState({
      showOptions: !this.state.showOptions,
    })
  }
  tld_user = tld => {
    this.props.domain_actions.RX__tld_add(tld, 'user')
  }
  tld_check = tld => {
    this.props.domain_actions.RX__tld_add(tld, 'checked')
  }
  tld_uncheck = tld => {
    this.props.domain_actions.RX__tld_add(tld, 'unchecked')
  }

  render() {
    // //window.doneLoading('render')

    /*
     * Content
     */
    //
    // Default content
    let PageContent = null
    let PageSearch = null
    if (!this.props.input_str) {
      /*
       * show Homepage
       */
      PageContent = <DomainsHome className="DomainsHome" that={this} />
      PageSearch = (
        <Search
          {...this.props}
          className={'Search SearchHome'}
          domains={true}
          title="Domain Name Suggestions:"
          cue={[
            <div key="4" className="cue-extra-line show-large">
              sentiment analysis,&thinsp;
              markov strings,&thinsp;
              translations,&thinsp;
              word hacks,&thinsp;
              locations,&thinsp;
            </div>,
            <span key="1">
              1000 <span className="color-accent u">extensions</span>,
              10000000 <span className="color-accent u">synonyms</span>,&nbsp;
            </span>,
            <span key="100">
              one <span className="color-accent u">API</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            </span>,
          ]}
        />
      )
    } else {
      /*
       * show Results
       */
      let cues = undefined
      if (!this.state.gotAvailability) {
        cues = [<span key="1">Loading domain availability...</span>]
      }
      PageContent = <DomainsResults className="DomainsResults" that={this} />
      PageSearch = (
        <Search
          {...this.props}
          className={'Search'}
          domains={true}
          title="Domain Name Suggestions"
          title_nav={[
            <span key="0" className="hide-small">
              <span className="color-subtle" style={{ display: 'inline-block', transform: 'scale(0.85)', transformOrigin: 'right' }}>
                get this&thinsp;
              </span>
              <span className="color-white" style={{ textDecoration: 'underline' }}>
                API
              </span>
              <FA
                icon={faArrowRight}
                className="faArrowRight color-subtle-deep"
                style={{ transform: 'scale(0.85)', paddingLeft: '0.25rem' }}
              />
            </span>,
          ]}
          cue={cues}
        />
      )
    }

    /*
     * View
     */
    return (
      <>
        {PageSearch}
        <StyledPage>{PageContent}</StyledPage>
      </>
    )
  }
}

const mapDispatchToProps = dispatch => {
  return {
    ui_actions: bindActionCreators(ui_actions, dispatch),
    domain_actions: bindActionCreators(domain_actions, dispatch),
    word_actions: bindActionCreators(word_actions, dispatch),
  }
}

const mapStateToProps = function(state) {
  return {
    // word
    search_options: state.word.search_options,
    // word
    word_chunks: state.word.chunks,
    word_input: state.word.input,
    input_str: state.word.input_str,
    // results
    hide_available: state.domain.hide_available,
    hide_unavailable: state.domain.hide_unavailable,
    hide_other: state.domain.hide_other,
    hide_premium: state.domain.hide_premium,
    // tlds
    tlds_all: state.domain.tlds_all,
    tlds_user: state.domain.tlds_user,
    tlds_checked: state.domain.tlds_checked,
    tlds_unchecked: state.domain.tlds_unchecked,
    tlds_unused: state.domain.tlds_unused,
    // suggestions
    suggestions_availability: state.domain.suggestions_availability,
    suggestions_domains: state.domain.suggestions_domains,
    suggestions_phrases: state.domain.suggestions_phrases,
    suggestions_phrase_lists: state.domain.suggestions_phrase_lists,
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Domains)
