export const ColorsStyled = styled.div`
  // has not been checked yet
  .domname.status0,
  .domname.status0 .ant-checkbox {
    color: var(--color-dark);
  }
  // checked, but unknown
  .domname.status-1,
  .domname.status-1 .ant-checkbox {
    color: var(--color-dark);
  }
  // default text
  .domname {
    color: hsl(120,75%,30%);
  }
  // available
  .domname.status1,
  .domname.status1 .ant-checkbox {
    color: hsl(120, 75%, 33%);
  }
  // premium
  .domname.status2,
  .domname.status2 .ant-checkbox {
    color: hsl(110, 67%, 67%);
  }
  // unknown
  .domname.status9,
  .domname.status9 .ant-checkbox {
    color: hsl(40, 55%, 70%);
  }
  // unavailable
  .domname.status10,
  .domname.status10 .ant-checkbox {
    color: hsl(0, 0%, 70%);
  }
  
  /*
   * domain suggestions (while getting availability, until received results)
   */
  &.gotAvailability {
    // hide available
    &.hideAvailable {
      .domname.status1 {
        display: none;
      }
    }
    // hide unavailable
    &.hideUnavailable {
      .domname.status10 {
        display: none;
      }
    }
    // hide premium
    &.hidePremium {
      .domname.status2 {
        display: none;
      }
    }
    // hide unknown
    &.hideOther {
      // hide all except already colored ones
      .domname:not(.status1):not(.status2):not(.status3):not(.status4):not(.status10) {
        display: none;
      }
    }
  }
`
