import React from 'react'
import { LinkToToggleOptionsStyled } from './Options.styled'
import { DomainsResultsStyled, ColorsStyled } from './Domains.styled'
import Options from './Options'
import Legend from './Legend'
import Tlds from './Tlds'
import Doms from '../../components/DomainsResults/Doms'
import { faAngleDown, faAngleUp } from '@fortawesome/pro-regular-svg-icons'
import { FontAwesomeIcon as FA } from '@fortawesome/react-fontawesome'

const DomainsResults = function(props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that
  /*
   * View variables:
   */
  let { suggestions_domains, suggestions_phrase_lists, suggestions_phrases, suggestions_availability } = that.props
  /*
   * Validate
   */
  if (!suggestions_domains || !Object.keys(suggestions_domains).length) {
    return (
      <DomainsResultsStyled>
        <div className="content">
          <p>No results...</p>
        </div>
      </DomainsResultsStyled>
    )
  }

  return (
    <>

      {/*
       * Options
       */}
      <Options that={that} />

      {/*
       * BODY / CONTENT / RESULTS
       */}
      <DomainsResultsStyled>
        <div className="content">

          {/*
           * Legend
           */}
          {!that.state.showWip &&
          <Legend that={that} />
          }

          {/*
           * Show WIP
           */}
          <LinkToToggleOptionsStyled
            className="LinkToToggleOptionsStyled"
            onClick={that.toggleWip}
          >
          {that.state.showWip
            ? <><span className="hide-small">hide&nbsp;</span>WIP <FA icon={faAngleUp} className="faAngleUp" /></>
            : <><span className="hide-small">show&nbsp;</span>WIP <FA icon={faAngleDown} className="faAngleDown" /></>
          }
          </LinkToToggleOptionsStyled>

          {/*
           * Columns
           */}
          <ColorsStyled
            className={
              (!that.state.gotAvailability ? ' gettingAvailability' : ' gotAvailability') +
              (that.props.hide_available ? ' hideAvailable' : '') +
              (that.props.hide_unavailable ? ' hideUnavailable' : '') +
              (that.props.hide_other ? ' hideOther' : '') +
              (that.props.hide_premium ? ' hidePremium' : '')
            }
          >
            {/*
             * WIP
             */}
            {!!that.state.showWip && (
              <div className="wip doms columns">

                {/*
                 * PHRASES (debugging, temporary)
                 */}
                <div className="column">
                  <div className="nowrap">
                    <b>results</b>
                  </div>
                  <div className="pre">
                        {suggestions_phrases.map((phrase, pi) => (
                          <div key={pi}>&quot;{phrase}&quot;</div>
                        ))}
                  </div>
                </div>

                {/*
                 * WIP PHRASE LISTS (debugging, temporary)
                 */}
                {Object.keys(suggestions_phrase_lists || {}).map((title, i) => {
                  let lists = suggestions_phrase_lists[title]
                  return (
                    <div key={title + i} className="column">
                      <div className="nowrap">
                        <b>{title}</b>
                      </div>
                      <div>
                        {lists.map((phrase, pi) => (
                          <div key={pi}>&quot;{phrase.join(' ')}&quot;</div>
                        ))}
                      </div>
                    </div>
                  )
                })}
              </div>
            )}

            {/*
             * Results:
             */}
            <Doms suggestions_domains={suggestions_domains} suggestions_availability={suggestions_availability} />

            {/*
             * TLDs
             */}
            {!that.state.showWip && (
              <Tlds that={that} />
            )}
          </ColorsStyled>
        </div>
      </DomainsResultsStyled>
    </>
  )
}

export default DomainsResults
